<template>
  <div class="hello">
    <h1 v-if="isLoading">Loading...</h1>
    <div v-if="!isLoading">
      <h1>Aperture Dashboard</h1>
      <h3>Aggregated from positions:</h3>
      <p>{{ getCompletedPositionQueries }} / {{ getNumPositions }}</p>
      <h3>Total value of open positions (TVL)</h3>
      <p>{{ getTVL }}</p>
      <h3>Total initial investment size of open positions</h3>
      <p>{{ getSumOpenPositionInitialSizes }}</p>
      <h3>Total initial investment size of ALL positions</h3>
      <p>{{ getSumAllPositionInitialSizes }}</p>
      <h3>Total contribution to Spectrum TVL from open positions</h3>
      <p>{{ getSumOpenPositionSpectrumTVLContributions }}</p>
      <h3>Sum of unrealized gain across open positions with unrealized gain</h3>
      <p>{{ getSumUnrealizedGain }}</p>
      <h3>Sum of short farm UST value across open positions</h3>
      <p>{{ getSumShortFarmValueAcrossOpenPositions }}</p>
      <h3>
        Sum of short farm UST value across open positions with unrealized gain
      </h3>
      <p>
        {{ getSumShortFarmValueAcrossOpenPositionsWithUnrealizedGain }}
      </p>

      <h2><br /><br />Open Positions</h2>
      <vue-good-table
        class="center"
        :columns="openPositionTableColumns"
        :rows="openPositionTableData"
        :sort-option="{
          enabled: true,
          initialSortBy: { field: 'id', type: 'asc' },
        }"
        :search-options="{
          enabled: true,
        }"
        :pagination-options="{
          enabled: true,
          perPageDropdown: [10, 20, 50, 100],
          jumpFirstOrLast: true,
        }"
      />

      <h2><br /><br />Closed Positions</h2>
      <vue-good-table
        class="center"
        :columns="closedPositionTableColumns"
        :rows="closedPositionTableData"
        :sort-option="{
          enabled: true,
          initialSortBy: { field: 'id', type: 'asc' },
        }"
        :search-options="{
          enabled: true,
        }"
        :pagination-options="{
          enabled: true,
          perPageDropdown: [10, 20, 50, 100],
          jumpFirstOrLast: true,
        }"
      />

      <GChart
        type="BarChart"
        style="height: 600px"
        :data="getCountChartData"
        :options="countChartOptions"
      />
      <GChart
        type="BarChart"
        style="height: 600px"
        :data="getSumChartData"
        :options="sumChartOptions"
      />
      <h3>TVL per mAsset</h3>
      <table style="margin-left: auto; margin-right: auto">
        <tr>
          <th>mAsset</th>
          <th>TVL</th>
        </tr>
        <tr v-for="(key, value) in mAssetToTVL" :key="value">
          <td>{{ value }}</td>
          <td>{{ key.toFormat(6) }}</td>
        </tr>
      </table>

      <h3>Total Positions</h3>
      <p>{{ openedPositions.length + closedPositions.length }}</p>
      <h3>Open Positions</h3>
      <ul>
        <li>
          {{ openedPositions.length }}
        </li>
      </ul>
      <h3>Total Closed Positions</h3>
      <ul>
        <li>
          {{ closedPositions.length }}
        </li>
      </ul>
      <h3>Unique Position Holders: {{ Object.keys(uniqueWallets).length }}</h3>
      <ul>
        <li v-for="(key, value) in uniqueWallets" :key="value">
          {{ value }}: {{ key }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { GChart } from "vue-google-charts";
import { LCDClient } from "@terra-money/terra.js";
import { BigNumber } from "bignumber.js";
import axios from "axios";
import pool from "@ricokahler/pool";
import "vue-good-table/dist/vue-good-table.css";
import { VueGoodTable } from "vue-good-table";

BigNumber.config({
  FORMAT: {
    // string to prepend
    prefix: "$",
    // decimal separator
    decimalSeparator: ".",
    // grouping separator of the integer part
    groupSeparator: ",",
    // primary grouping size of the integer part
    groupSize: 3,
    // secondary grouping size of the integer part
    secondaryGroupSize: 0,
    // grouping separator of the fraction part
    fractionGroupSeparator: " ",
    // grouping size of the fraction part
    fractionGroupSize: 0,
    // string to append
    suffix: "",
  },
});

const mAssetMap = {
  terra1vxtwu4ehgzz77mnfwrntyrmgl64qjs75mpwqaz: "mAAPL",
  terra14y5affaarufk3uscy2vr6pe6w6zqf2wpjzn5sh: "mTSLA",
  terra1jsxngqasf2zynj5kyh0tgq9mj3zksa5gk35j4k: "mNFLX",
  terra1dk3g53js3034x4v5c3vavhj2738une880yu6kx: "mETH",
  terra1csk6tc7pdmpr782w527hwhez6gfv632tyf72cp: "mQQQ",
  terra1cc3enj9qgchlrj34cnzhwuclc4vl2z3jl7tkqg: "mTWTR",
  terra1227ppwxxj3jxz8cfgq00jgnxqcny7ryenvkwj6: "mMSFT",
  terra165nd2qmrtszehcfrntlplzern7zl4ahtlhd5t2: "mAMZN",
  terra1w7zgkcyt7y4zpct9dw8mw362ywvdlydnum2awa: "mBABA",
  terra10h7ry7apm55h4ez502dqdv9gr53juu85nkd4aq: "mIAU",
  terra1kscs6uhrqwy6rx5kuw5lwpuqvm3t6j2d6uf2lp: "mSLV",
  terra1lvmx8fsagy70tv0fhmfzdw9h6s3sy4prz38ugf: "mUSO",
  terra19cmt6vzvhnnnfsmccaaxzy2uaj06zjktu6yzjx: "mVIXY",
  terra1mqsjugsugfprn3cvgxsrr8akkvdxv2pzc74us7: "mFB",
  terra1h8arz2k547uvmpxctuwush3jzc8fun4s96qgwt: "mGOOGL",
  terra18wayjpyq28gd970qzgjfmsjj7dmgdk039duhph: "mCOIN",
  terra18yqdfzfhnguerz9du5mnvxsh5kxlknqhcxzjfr: "mHOOD",
  terra1qqfx5jph0rsmkur2zgzyqnfucra45rtjae5vh6: "mARKK",
  terra1l5lrxtwd98ylfy09fn866au6dp76gu8ywnudls: "mGLXY",
  terra1u43zu5amjlsgty5j64445fr9yglhm53m576ugh: "mSQ",
  terra1g4x2pzmkc9z3mseewxf758rllg08z3797xly0n: "mABNB",
  terra1aa00lpfexyycedfg5k2p60l9djcmw0ue5l8fhc: "mSPY",
  terra19ya4jpvjvvtggepvmmj6ftmwly3p7way0tt08r: "mDOT",
  terra18ej5nsuu867fkx4tuy2aglpvqjrkcrjjslap3z: "mAMD",
  terra137drsu8gce5thf6jr5mxlfghw36rpljt3zj73v: "mGS",
  terra1qsnj5gvq8rgs7yws8x5u02gwd5wvtu4tks0hjm: "mKO",
  terra1rh2907984nudl7vh56qjdtvv7947z4dujj92sx: "mPYPL",
  terra1246zy658dfgtausf0c4a6ly8sc2e285q4kxqga: "mSBUX",
  terra1ptdxmj3xmmljzx02nr4auwfuelmj0cnkh8egs2: "mJNJ",
  terra1rhhvx8nzfrx5fufkuft06q5marfkucdqwq5sjw: "mBTC",
  terra1drsjzvzej4h4qlehcfwclxg4w5l3h5tuvd3jd8: "mNVDA",
  terra1dj2cj02zak0nvwy3uj9r9dhhxhdwxnw6psse6p: "mNIO",
  terra149755r3y0rve30e209awkhn5cxgkn5c8ju9pm5: "mDIS",
  terra17ana8hvzea0q7w367dm0dw48sxwql39qekpt7g: "mNKE",
};
const chartRangeLimits = [
  500, 1000, 2000, 5000, 10000, 20000, 50000, 100000, 200000, 500000, 1e100,
];

// Generates array [begin, begin + 1, begin + 2, ..., end - 1].
function generateRangeArray(begin, end) {
  if (begin >= end) return [];
  return [...Array(end - begin).keys()].map((num) => num + begin);
}

export default {
  name: "Metrics",
  props: {},
  components: {
    GChart,
    VueGoodTable,
  },
  async mounted() {
    this.init();
    await this.crunchData();
  },
  data: function () {
    return {
      client: undefined,
      uniqueWallets: {},
      openedPositions: [],
      closedPositions: [],
      initialInvestmentUSTOpenPositions: [],
      initialInvestmentUSTAllPositions: [],
      numPositions: 0,
      completedPositionQueries: 0,
      // Sum of the current uusd value of open positions.
      tvl: BigNumber(0),
      // Sum of initial uusd investment sizes of open positions.
      sumOpenPositionInitialSizes: BigNumber(0),
      // Sum of initial uusd investment sizes of all positions.
      sumAllPositionInitialSizes: BigNumber(0),
      // Sum of Spectrum-staked long farm values of open positions.
      sumOpenPositionSpectrumTVLContributions: BigNumber(0),
      // Sum of unrealized gain across all open positions with a positive unrealized gain.
      sumUnrealizedGain: BigNumber(0),
      // Sum of short farm value (uusd) across all open positions.
      sumShortFarmValueAcrossOpenPositions: BigNumber(0),
      // Sum of short farm value (uusd) across open positions with a positive unrealized gain.
      sumShortFarmValueAcrossOpenPositionsWithUnrealizedGain: BigNumber(0),
      mAssetToTVL: {},
      countChartOptions: {
        title: "Initial Investment UST Size (bucketed counts)",
        bars: "horizontal",
      },
      sumChartOptions: {
        title: "Initial Investment UST Size (bucketed sum)",
        bars: "horizontal",
      },
      isLoading: true,
      openPositionTableColumns: [
        { label: "ID", field: "id", type: "number" },
        {
          label: "Opened at",
          field: "creationTime",
          formatFn: function (date) {
            return new Date(date).toLocaleString();
          },
        },
        {
          label: "mAsset",
          field: "mAsset",
        },
        {
          label: "Initial Value",
          field: "initValue",
          type: "number",
          formatFn: function (amount) {
            return BigNumber(amount).toFormat(6);
          },
        },
        {
          label: "Current Value",
          field: "curValue",
          type: "number",
          formatFn: function (amount) {
            return BigNumber(amount).toFormat(6);
          },
        },
        {
          label: "P/L percentage",
          field: "profitRatio",
          type: "percentage",
        },
        {
          label: "APR",
          field: "apr",
          type: "percentage",
        },
        {
          label: "Target CR",
          field: "tcr",
          sortable: false,
        },
        {
          label: "Current CR",
          field: "cr",
          type: "percentage",
        },
        {
          label: "Holder",
          field: "holder",
          html: true,
          sortable: false,
        },
        {
          label: "Contract",
          field: "contract",
          html: true,
          sortable: false,
        },
      ],
      closedPositionTableColumns: [
        { label: "ID", field: "id", type: "number" },
        {
          label: "Opened at",
          field: "creationTime",
          formatFn: function (date) {
            return new Date(date).toLocaleString();
          },
        },
        {
          label: "Closed at",
          field: "closureTime",
          formatFn: function (date) {
            return new Date(date).toLocaleString();
          },
        },
        {
          label: "mAsset",
          field: "mAsset",
        },
        {
          label: "Initial Value",
          field: "initValue",
          type: "number",
          formatFn: function (amount) {
            return BigNumber(amount).toFormat(6);
          },
        },
        {
          label: "Final Value (amount disbursed)",
          field: "finalValue",
          type: "number",
          formatFn: function (amount) {
            return BigNumber(amount).toFormat(6);
          },
        },
        {
          label: "P/L percentage",
          field: "profitRatio",
          type: "percentage",
        },
        {
          label: "APR",
          field: "apr",
          type: "percentage",
        },
        {
          label: "Holder",
          field: "holder",
          html: true,
          sortable: false,
        },
        {
          label: "Contract",
          field: "contract",
          html: true,
          sortable: false,
        },
      ],
      openPositionTableData: [],
      closedPositionTableData: [],
    };
  },
  computed: {
    getCountChartData() {
      const chartData = [
        ["Range", "Active Positions (count)", "All Positions (count)"],
        ["(0, $500]", 0, 0],
        ["($500, $1,000]", 0, 0],
        ["($1,000, 2,000]", 0, 0],
        ["($2,000, 5,000]", 0, 0],
        ["($5,000, 10,000]", 0, 0],
        ["($10,000, 20,000]", 0, 0],
        ["($20,000, 50,000]", 0, 0],
        ["($50,000, 100,000]", 0, 0],
        ["($100,000, 200,000]", 0, 0],
        ["($200,000, 500,000]", 0, 0],
        ["($500,000, Infinity)", 0, 0],
      ];

      // Aggregate count from active positions.
      for (const value of this.initialInvestmentUSTOpenPositions) {
        let index = 0;
        for (const limit of chartRangeLimits) {
          if (value <= limit) break;
          index++;
        }
        chartData[index + 1][1]++;
      }

      // Aggregate count from all positions.
      for (const value of this.initialInvestmentUSTAllPositions) {
        let index = 0;
        for (const limit of chartRangeLimits) {
          if (value <= limit) break;
          index++;
        }
        chartData[index + 1][2]++;
      }

      return chartData;
    },
    getSumChartData() {
      const chartData = [
        ["Range", "Active Positions (sum)", "All Positions (sum)"],
        ["(0, $500]", 0, 0],
        ["($500, $1,000]", 0, 0],
        ["($1,000, 2,000]", 0, 0],
        ["($2,000, 5,000]", 0, 0],
        ["($5,000, 10,000]", 0, 0],
        ["($10,000, 20,000]", 0, 0],
        ["($20,000, 50,000]", 0, 0],
        ["($50,000, 100,000]", 0, 0],
        ["($100,000, 200,000]", 0, 0],
        ["($200,000, 500,000]", 0, 0],
        ["($500,000, Infinity)", 0, 0],
      ];

      // Aggregate sum from active positions.
      for (const value of this.initialInvestmentUSTOpenPositions) {
        let index = 0;
        for (const limit of chartRangeLimits) {
          if (value <= limit) break;
          index++;
        }
        chartData[index + 1][1] += value;
      }

      // Aggregate sum from all positions.
      for (const value of this.initialInvestmentUSTAllPositions) {
        let index = 0;
        for (const limit of chartRangeLimits) {
          if (value <= limit) break;
          index++;
        }
        chartData[index + 1][2] += value;
      }

      return chartData;
    },
    getNumPositions() {
      return this.numPositions;
    },
    getCompletedPositionQueries() {
      return this.completedPositionQueries;
    },
    getTVL() {
      return this.tvl.dividedBy(BigNumber(1e6)).toFormat(6);
    },
    getSumOpenPositionInitialSizes() {
      return this.sumOpenPositionInitialSizes
        .dividedBy(BigNumber(1e6))
        .toFormat(6);
    },
    getSumAllPositionInitialSizes() {
      return this.sumAllPositionInitialSizes
        .dividedBy(BigNumber(1e6))
        .toFormat(6);
    },
    getSumOpenPositionSpectrumTVLContributions() {
      return this.sumOpenPositionSpectrumTVLContributions
        .dividedBy(BigNumber(1e6))
        .toFormat(6);
    },
    getSumUnrealizedGain() {
      return this.sumUnrealizedGain.dividedBy(BigNumber(1e6)).toFormat(6);
    },
    getSumShortFarmValueAcrossOpenPositions() {
      return this.sumShortFarmValueAcrossOpenPositions
        .dividedBy(BigNumber(1e6))
        .toFormat(6);
    },
    getSumShortFarmValueAcrossOpenPositionsWithUnrealizedGain() {
      return this.sumShortFarmValueAcrossOpenPositionsWithUnrealizedGain
        .dividedBy(BigNumber(1e6))
        .toFormat(6);
    },
  },
  methods: {
    init() {
      console.log("initializing...");
      this.client = new LCDClient({
        URL: "https://lcd.terra.dev/",
        chainID: "columbus-5",
      });
      for (var mAssetAddr in mAssetMap) {
        this.mAssetToTVL[mAssetMap[mAssetAddr]] = BigNumber(0);
      }
    },
    async crunchData() {
      if (!this.client) {
        console.log("Client is uninitialized.");
        return;
      }

      // Parse and validate.
      var terra_manager = "terra1ajkmy2c0g84seh66apv9x6xt6kd3ag80jmcvtz";
      var connection = this.client;

      // Get next position id to establish limit.
      const next_position_res = await connection.wasm.contractQuery(
        terra_manager,
        {
          get_next_position_id: {},
        }
      );
      this.numPositions = next_position_res.next_position_id;
      console.log("next position id: ", next_position_res.next_position_id);

      // Get all position info.
      let numPositions = parseInt(next_position_res.next_position_id);
      let hiveQueryBatchSize = 500;
      let hiveQueryNumBatches = Math.ceil(numPositions / hiveQueryBatchSize);
      let allPositionsInfo = (
        await pool({
          collection: generateRangeArray(0, hiveQueryNumBatches),
          maxConcurrency: 5,
          task: async (batchId) => {
            let startPositionId = batchId * hiveQueryBatchSize;
            let endPositionId = Math.min(
              startPositionId + hiveQueryBatchSize,
              numPositions
            );
            let hiveQuery =
              "{" +
              generateRangeArray(startPositionId, endPositionId)
                .map((position_id) => {
                  return (
                    "q" +
                    position_id +
                    ':wasm{contractQuery(contractAddress:"terra1jvehz6d9gk3gl4tldrzd8qzj8zfkurfvtcg99x"query:{batch_get_position_info:{positions:[{chain_id:3,position_id:"' +
                    position_id.toString() +
                    '"}]}})}'
                  );
                })
                .join() +
              "}";
            let hiveResponse = await axios({
              method: "post",
              url: "https://solitary-wind-b421.hyperfocal-dev.workers.dev/graphql",
              data: {
                query: hiveQuery,
              },
            });
            return Object.values(hiveResponse.data.data).map(
              (element) => element.contractQuery.items[0]
            );
          },
        })
      ).flat();

      // Generate position info table data.
      this.openPositionTableData = allPositionsInfo
        .filter(
          (positionInfo) => positionInfo.info.position_close_info === null
        )
        .map((positionInfo) => {
          return {
            id: parseInt(positionInfo.position.position_id),
            mAsset: mAssetMap[positionInfo.info.mirror_asset_cw20_addr],
            initValue:
              BigNumber(positionInfo.info.position_open_info.uusd_amount) / 1e6,
            curValue:
              BigNumber(positionInfo.info.detailed_info.uusd_value) / 1e6,
            creationTime: new Date(
              positionInfo.info.position_open_info.time_nanoseconds / 1e6
            ),
            cr: parseFloat(positionInfo.info.detailed_info.collateral_ratio),
            tcr:
              positionInfo.info.detailed_info.target_collateral_ratio_range
                .min +
              " ~ " +
              positionInfo.info.detailed_info.target_collateral_ratio_range.max,
            holder:
              '<a href="https://terrasco.pe/mainnet/address/' +
              positionInfo.holder +
              '">Holder</a>',
            contract:
              '<a href="https://terrasco.pe/mainnet/address/' +
              positionInfo.contract +
              '">Contract</a>',
            generateAdditionalFields: function () {
              this.profitRatio =
                (this.curValue - this.initValue) / this.initValue;
              if (this.profitRatio > 0) {
                let timeElapsedDays =
                  (Date.now() - this.creationTime) / 1000 / 3600 / 24;
                this.apr = (this.profitRatio / timeElapsedDays) * 365;
              }
              return this;
            },
          }.generateAdditionalFields();
        });
      this.closedPositionTableData = allPositionsInfo
        .filter(
          (positionInfo) => positionInfo.info.position_close_info !== null
        )
        .map((positionInfo) => {
          return {
            id: parseInt(positionInfo.position.position_id),
            mAsset: mAssetMap[positionInfo.info.mirror_asset_cw20_addr],
            initValue:
              BigNumber(positionInfo.info.position_open_info.uusd_amount) / 1e6,
            finalValue:
              BigNumber(positionInfo.info.position_close_info.uusd_amount) /
              1e6,
            creationTime: new Date(
              positionInfo.info.position_open_info.time_nanoseconds / 1e6
            ),
            closureTime: new Date(
              positionInfo.info.position_close_info.time_nanoseconds / 1e6
            ),
            holder:
              '<a href="https://terrasco.pe/mainnet/address/' +
              positionInfo.holder +
              '">Holder</a>',
            contract:
              '<a href="https://terrasco.pe/mainnet/address/' +
              positionInfo.contract +
              '">Contract</a>',
            generateAdditionalFields: function () {
              this.profitRatio =
                (this.finalValue - this.initValue) / this.initValue;
              if (this.profitRatio > 0) {
                let timeElapsedDays =
                  (this.closureTime - this.creationTime) / 1000 / 3600 / 24;
                this.apr = (this.profitRatio / timeElapsedDays) * 365;
              }
              return this;
            },
          }.generateAdditionalFields();
        });

      allPositionsInfo.map((positionResult) => {
        this.processPositionResult(positionResult);
      });
      this.isLoading = false;

      this.openedPositions.sort();
      this.closedPositions.sort();
      this.isMounted = true;
    },
    processPositionResult(position_result) {
      const holder = position_result.holder;
      if (holder in this.uniqueWallets) {
        this.uniqueWallets[holder]++;
      } else {
        this.uniqueWallets[holder] = 1;
      }
      const position_info = position_result.info;
      const position_id = position_result.position.position_id;

      // Process initial investment size.
      const initialInvestmentUusd =
        position_info.position_open_info.uusd_amount;
      this.sumAllPositionInitialSizes = this.sumAllPositionInitialSizes.plus(
        initialInvestmentUusd
      );
      const initialInvestmentUST = BigNumber(initialInvestmentUusd) / 1e6;
      this.initialInvestmentUSTAllPositions.push(initialInvestmentUST);

      if (position_info.detailed_info == null) {
        this.closedPositions.push(position_id);
      } else {
        this.openedPositions.push(position_id);
        this.initialInvestmentUSTOpenPositions.push(initialInvestmentUST);

        // Process initial investment size of this position which is still open.
        this.sumOpenPositionInitialSizes =
          this.sumOpenPositionInitialSizes.plus(
            position_info.position_open_info.uusd_amount
          );
        // Process long farm value, which is uusd_long_farm * 2.
        this.sumOpenPositionSpectrumTVLContributions =
          this.sumOpenPositionSpectrumTVLContributions.plus(
            BigNumber(
              position_info.detailed_info.state.uusd_long_farm
            ).multipliedBy(2)
          );
        // Process short farm value.
        const shortFarmValue = BigNumber(
          position_info.detailed_info.state.mirror_asset_short_amount
        ).times(
          BigNumber(position_info.detailed_info.state.mirror_asset_oracle_price)
        );
        this.sumShortFarmValueAcrossOpenPositions =
          this.sumShortFarmValueAcrossOpenPositions.plus(shortFarmValue);
        // Process sums for open position with a positive unrealized gain.
        if (
          BigNumber(position_info.detailed_info.uusd_value) >
          BigNumber(position_info.position_open_info.uusd_amount)
        ) {
          this.sumShortFarmValueAcrossOpenPositionsWithUnrealizedGain =
            this.sumShortFarmValueAcrossOpenPositionsWithUnrealizedGain.plus(
              shortFarmValue
            );
          this.sumUnrealizedGain = this.sumUnrealizedGain.plus(
            BigNumber(position_info.detailed_info.uusd_value) -
              BigNumber(position_info.position_open_info.uusd_amount)
          );
        }
        // Process overall TVL.
        this.tvl = this.tvl.plus(
          BigNumber(position_info.detailed_info.uusd_value)
        );
        // Process per-mAsset TVL.
        const mAssetAddr = position_info.mirror_asset_cw20_addr;
        const mAssetName = mAssetMap[mAssetAddr];
        if (mAssetName in this.mAssetToTVL) {
          this.mAssetToTVL[mAssetName] = this.mAssetToTVL[mAssetName].plus(
            BigNumber(position_info.detailed_info.uusd_value).dividedBy(
              BigNumber(1e6)
            )
          );
        } else {
          this.mAssetToTVL[mAssetName] = BigNumber(
            position_info.detailed_info.uusd_value
          ).dividedBy(BigNumber(1e6));
        }
      }

      this.completedPositionQueries++;
    },
  },
};
</script>

